___CSS_LOADER_EXPORT___.push([module.id, `* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  /* outline: 1px solid red; */
}

html,
body {
  min-height: 100%;
}

body {
  font-family: "Lumanosimo";
  background-color: antiquewhite;
  padding: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
}
form {
  border: none;
  border-radius: 10px;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  max-width: 400px;
  width: 90%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
form .heading {
  font-size: 1.8rem;
  display: flex;
  justify-content: center;
  color: darkmagenta;
  font-family: "Merienda";
  font-weight: bolder;
  margin-bottom: 20px;
}
form p {
  display: flex;
  flex-direction: column;
  gap: 5px;
  margin-bottom: 10px;
}
form label {
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 5px;
}
form label[for="show-pass"],
form label[for="show-pass-two"]{
  font-size: 0.8rem;
  display: flex;
  gap: 5px;
  font-weight: normal;
  font-family: "Merienda";
}
form input,
select#user-country {
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
  font-family: "Merienda";
}
input[type="checkbox"] {
      accent-color: #6a0dad;
    }
form button {
  padding: 10px;
  margin-top: 10px;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  background-color: #6a0dad;
  color: white;
  font-size: 1rem;
  font-family: "Poppins";
}

form button:hover {
  background-color: #8a2be2;
}
input:invalid {
  border-color: #900;
  background-color: #fdd;
}

input:focus:invalid {
  outline: none;
}
.email-error,
.postal-code-error,
.password-error,
.confirmation-password-error {
  width: 100%;
  padding: 0;
  font-size: 80%;
  color: white;
  background-color: #900;
  border-radius: 0 0 5px 5px;
  box-sizing: border-box;
  font-family: "Merienda";
}

.email-error.active,
.postal-code-error.active,
.password-error.active,
.confirmation-password-error.active{
  padding: 0.3em;
}
#confirm-user-password.error{
  border-color: #900;
  background-color: #fdd;
}
#confirm-user-password.error:focus{
  outline: none;
}
